import * as React from "react"
import { Box, Table, Flex, Badge, Button } from '@radix-ui/themes';
import { MinusIcon, PlusIcon } from '@radix-ui/react-icons';
import MedalSvg from './MedalSvg';

function Medal(props) {
    const { country, medal, onIncrement, onDecrement } = props;

    return (
        <Table.Row>
            <Table.RowHeaderCell>
                <Flex align="center">
                    <MedalSvg color={medal.color} />
                    <Box pl="2">{medal.name} Medals</Box>
                </Flex>
            </Table.RowHeaderCell>
            <Table.Cell align="right" width="108px">
                <Flex align="center" justify="between">
                    <Button variant="ghost" disabled={ country[medal.name] === 0 }>
                        <MinusIcon onClick={() => country[medal.name] > 0 && onDecrement(country.id, medal.name)} />
                    </Button>
                    <Badge variant="outline">{ country[medal.name] }</Badge>
                    <Button variant="ghost">
                        <PlusIcon onClick={() => onIncrement(country.id, medal.name)} />
                    </Button>
                </Flex>
            </Table.Cell>
        </Table.Row>
    )
}

export default Medal
