import * as React from "react"
import { Box, Table, Flex, Badge, Button } from '@radix-ui/themes';
import { TrashIcon } from '@radix-ui/react-icons';
import Medal from "./Medal";

function Country(props) {
    const { country, medals, onIncrement, onDecrement, onDelete } = props;

    function getMedalsTotal() {
        let sum = 0;
        medals.forEach(medal => { sum += country[medal.name]; });
        return sum;
    }
    return (
        <Box width="300px" p="2">
            <Table.Root variant="surface">
                <Table.Header>
                    <Table.Row>
                        <Table.ColumnHeaderCell colSpan="2">
                            <Flex justify="between">
                                <span>{country.name}<Badge variant="outline" ml="2">{getMedalsTotal(country, medals)}</Badge></span>
                                <Button color="red" variant="ghost" size="1">
                                    <TrashIcon onClick={() => onDelete(country.id)} />
                                </Button>
                            </Flex>
                        </Table.ColumnHeaderCell>
                    </Table.Row>
                </Table.Header>

                <Table.Body>
                    {medals.sort((a, b) => a.rank - b.rank).map(medal =>
                        <Medal
                            key={medal.id}
                            medal={medal}
                            country={country}
                            onIncrement={onIncrement}
                            onDecrement={onDecrement}
                        />
                    )}
                </Table.Body>
            </Table.Root>
        </Box>
    )
}

export default Country
